import { http } from "src/common/";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";


export const setFormFilterMonev = (formType, formValue) => {
  return { type: "FILTER_PERTANYAAN", formType, formValue };
};

export const GetMonevByYearAction = (type, year) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMonev}/GetMonevs?Tahun=${year}`
      )
      .then((res) => {
        loadingStatus(false)
        let result = res.data;
        if (result.isSuccess) {
          dispatch({ type, payload: result.data });
        } else {
          Swal.fire("", `${result.returnMessage}  `, "error");
           dispatch({ type, payload: result.data });
        }
      });
  };
};
