import { http } from "src/common/helper";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";

export const SetPegawaiChangedAction = (formType, formValue) => {
  return { type: "SET_CHANGE_NAME", formType, formValue };
};

export const SetFirstFormAction = (formType, formValue) => {
  return { type: "SET_FIRST_ADD_FORM_MONEV", formType, formValue };
};
export const GetPegawiKecamatanAction = (idPegawai) => {
  return (dispatch) => {
    loadingStatus(true);
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMasterWillOrgs}/GetPegawaiKecamatan?IdPegawai=${idPegawai}`
      )
      .then((res) => {
        loadingStatus(false);
        let data = res.data;
        if (data.isSuccess) {
          dispatch(SetFirstFormAction("IdPegawai", data.data.idUser));
          dispatch(SetPegawaiChangedAction("FullName", data.data.fullName));
          dispatch(
            SetPegawaiChangedAction("MobileNumber", data.data.mobileNumber)
          );
          dispatch(SetPegawaiChangedAction("Email", data.data.email));
          dispatch(
            SetPegawaiChangedAction("OfficeAddress", data.data.officeAddress)
          );
        } else {
           dispatch(SetFirstFormAction("IdPegawai", 0));
           dispatch(SetPegawaiChangedAction("FullName", ""));
           dispatch(
             SetPegawaiChangedAction("MobileNumber", "")
           );
           dispatch(SetPegawaiChangedAction("Email", ""));
           dispatch(
             SetPegawaiChangedAction("OfficeAddress", "")
           );
          Swal.fire("Gagal", `${data.returnMessage}`, "error");

        }
      });
  };
};

export const saveToTemporaryAction = ( idPegawai, idJenisLokpri) =>{

  return (dispatch) => {
    loadingStatus(true);
    let idUser = sessionStorage.getItem("idUser");

    let param = `?IdUser=${idUser}&IdPegawai=${idPegawai}&IdJenisLokpri=${idJenisLokpri}`;
    http.post(
      `${process.env.REACT_APP_ROOT_API}${serviceName.svcMonev}/AddMonev${param}`
    ).then(res => {
      loadingStatus(false);
      let results = res.data;
      if (results.isSuccess) {
        let resultData = results.data;

        if(resultData.isEdit){
          Swal.fire({
              title: "",
              text: "Anda sudah menginputkan data sebelumnya, lanjutkan mengedit ?",
              icon: "info",
              showCancelButton: true,
              confirmButtonColor: "#3085d6",
              cancelButtonColor: "#d33",
              confirmButtonText: "Oke",
              cancelButtonText: "Batal",
            })
            .then((resultConfm) => {
              if(resultConfm.isConfirmed){
                dispatch({ type: "FORM_INPUT_FIRST", payload:true });
                dispatch({
                  type: "GET_RESULT_ADD_TEMPORARY",
                  payload: resultData,
                });
              }else{
                   dispatch({ type: "FORM_INPUT_FIRST", payload: false });
              }
            });
        }else{
          dispatch({ type: "FORM_INPUT_FIRST", payload: true });
            dispatch({
              type: "GET_RESULT_ADD_TEMPORARY",
              payload: resultData,
            });
        }
      } else {
        Swal.fire("Gagal", `${results.returnMessage}`, "error");
      }
    });
  }
}


export const SetGetJawabanLastAction = (
  idMonev,
  idVariabel,
  idIndikator,
  dataJawaban
) => {
  return (dispatch) => {
    loadingStatus(true);
    let idUser = sessionStorage.getItem("idUser");
    let param = `?IdUser=${idUser}&IdMonev=${idMonev}&IdVariabel=${idVariabel}&IdIndikator=${idIndikator}`;
    console.log(param);
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMonev}/SetGetJawaban${param}`,
        dataJawaban
      )
      .then((res) => {
        loadingStatus(false);
        console.log(res.data);
        let data = res.data;
        if (data.isSuccess) {
          Swal.fire(
            "Berhasil",
            `Selamat Anda Sudah berhasil input monev`,
            "success"
          );
          dispatch({ type: "FORM_INPUT_FIRST", payload: true });
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};
