import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import CIcon from "@coreui/icons-react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import MaterialTable from "material-table";
import Select from "react-select";
import makeAnimated from "react-select/animated";
import {
  SetDropdownRolesAction,
  GetUsersAPIAction,
  setFormUserAction,
  AddUserAPIAction,
  SetUserInActiveAction,
  SetUserActiveAction,
  GetUserAPIByIdUserAction,
  UpdateUserAPIAction,
} from "src/common/redux/action";
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CInput,
  CLabel,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow,
  CTextarea,
} from "@coreui/react";
const User = () => {
  /**
   * set management redux
   */
  const { dataComboRoles } = useSelector((state) => state.dropdownReducer);
  const { GetDataUsers, modalUser, formUserReduce } = useSelector(
    (state) => state.userReducer
  );
  const dispatch = useDispatch();
  const [stateUpdate, setStateUpdate] = useState(false);
  const allLoadAction = () => {
    dispatch(GetUsersAPIAction("GET_USER_LIST"));
  };
  const animatedComponents = makeAnimated();

  const {
    IdUser,
    Username,
    Password,
    Email,
    FirstName,
    MiddleName,
    LastName,
    Address,
    PhoneNumber,
    MobileNumber,
    FileImage,
    Roles,
  } = formUserReduce;

  useEffect(() => {
    allLoadAction();
  }, [dispatch]);
  const showModal = () => dispatch({ type: "MODAL_USER", payload: !modalUser });

  const handleModalUser = (type, idUser) => {
    dispatch(SetDropdownRolesAction("DATA_CMB_ROLES"));
    if (type === "add") {
      dispatch({ type: "RESET_FORM_USER" });
      showModal();
      setStateUpdate(false);
    } else {
      setStateUpdate(true);
      dispatch(GetUserAPIByIdUserAction(idUser));
      showModal();
    }
  };

  const handleAktiveAndNonAktive = (idUser, type, fullName) => {

    if(type === "NoActive"){
      dispatch(SetUserInActiveAction(idUser, fullName))
    }else{
      dispatch(SetUserActiveAction(idUser, fullName))
     }
  };

  const prosesuserAdd = () => {
    dispatch(AddUserAPIAction(formUserReduce));
  };

  const prosesUserEdit = () => dispatch(UpdateUserAPIAction(formUserReduce));

  const onImageChange = (event) => {
    const filePatch = event.target.files[0];
    if(filePatch !== undefined){
      dispatch(setFormUserAction("FileImage", event.target.files[0].name));
    }

  };

  const columns = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row d-flex justify-content-center">
            <button
              type="button"
              className="btn btn-sm btn-dark text-center mr-2"
              onClick={() => handleModalUser("edit", rowData.idUser)}
            >
              <CIcon name="cil-pencil" /> Update
            </button>
            {/* cek user active or not */}
            {rowData.strStatus === "Aktif" ? (
              <button
                className="btn btn-warning ml-1"
                onClick={() =>
                  handleAktiveAndNonAktive(
                    rowData.idUser,
                    "NoActive",
                    rowData.fullName
                  )
                }
              >
                {" "}
                Non Aktifkan
              </button>
            ) : (
              <button
                className="btn btn-warning ml-1"
                onClick={() =>
                  handleAktiveAndNonAktive(
                    rowData.idUser,
                    "Active",
                    rowData.fullName
                  )
                }
              >
                {" "}
                Aktifkan
              </button>
            )}
          </div>
        );
      },
    },
    { title: "Nama Lengkap", field: "fullName" },
    { title: "Alamat", field: "address" },
    { title: "No Telepone", field: "phoneNumber" },
    { title: "No Handphone", field: "mobileNumber" },
    {
      title: "Status",
      field: "strStatus",
      render: (rowData) =>
        rowData.strStatus === "Aktif" ? (
          <span className="badge badge-primary">{rowData.strStatus}</span>
        ) : (
          <span className="badge badge-dark">{rowData.strStatus}</span>
        ),
    },
    { title: "Role", field: "roleName" },
  ];
  return (
    <div>
      <CRow>
        <CCol md="12" xl="12" xs="12">
          <CCard>
            <CCardHeader>
              <CCol md="3">
                <CButton
                  block
                  color="primary"
                  onClick={() => handleModalUser("add")}
                >
                  <FontAwesomeIcon icon="plus" /> Tambah{" "}
                </CButton>
              </CCol>
            </CCardHeader>
            <CCardBody>
              <MaterialTable
                title=""
                option={{
                  headerStyle: {
                    backgroundColor: "#01579b",
                    color: "#FFF",
                  },
                  filtering: true,
                }}
                columns={columns}
                data={GetDataUsers.length > 0 ? GetDataUsers : []}
              />
            </CCardBody>
          </CCard>
        </CCol>

        {/* set modal user */}
        <CModal show={modalUser} onClose={showModal} size="lg">
          <CModalHeader closeButton>
            <CModalTitle>Form Pengguna</CModalTitle>
          </CModalHeader>
          <CModalBody>
            <CForm className="form-horizontal">
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-username" className="col-md-2">
                  Username
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="text"
                    placeholder="Input Username ..."
                    value={Username || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserAction("Username", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup
                className="form-row"
                style={{ display: stateUpdate ? "none" : "" }}
              >
                <CLabel htmlFor="nf-password" className="col-md-2">
                  Password
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="password"
                    placeholder="Input Password ..."
                    value={Password || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserAction("Password", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-email" className="col-md-2">
                  Email
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="email"
                    placeholder="Input Email ..."
                    value={Email || ""}
                    onChange={(event) =>
                      dispatch(setFormUserAction("Email", event.target.value))
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-fullname" className="col-md-2">
                  Nama Lengkap
                </CLabel>
                <div className="col-md-3">
                  <CInput
                    type="text"
                    placeholder="Input nama depan ..."
                    value={FirstName || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserAction("FirstName", event.target.value)
                      )
                    }
                  />
                </div>
                <div className="col-md-3">
                  <CInput
                    type="text"
                    placeholder="Input nama tengah ..."
                    value={MiddleName || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserAction("MiddleName", event.target.value)
                      )
                    }
                  />
                </div>
                <div className="col-md-3">
                  <CInput
                    type="text"
                    placeholder="Input nama akhir ..."
                    value={LastName || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserAction("LastName", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-address" className="col-md-2">
                  Alamat
                </CLabel>
                <div className="col-md-9">
                  <CTextarea
                    placeholder="Input Alamat"
                    value={Address || ""}
                    onChange={(event) =>
                      dispatch(setFormUserAction("Address", event.target.value))
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-notelepon" className="col-md-2">
                  No Telepon
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="text"
                    placeholder="Input no telepon ..."
                    value={PhoneNumber || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserAction("PhoneNumber", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-mobilenumber" className="col-md-2">
                  No Handphone
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="text"
                    placeholder="Input no hp ..."
                    value={MobileNumber || ""}
                    onChange={(event) =>
                      dispatch(
                        setFormUserAction("MobileNumber", event.target.value)
                      )
                    }
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-mobilenumber" className="col-md-2">
                  Upload Foto
                </CLabel>
                <div className="col-md-9">
                  <CInput
                    type="file"
                    onChange={(e) => onImageChange(e)}
                    placeholder="Input no hp ..."
                  />
                </div>
              </CFormGroup>
              <CFormGroup className="form-row">
                <CLabel htmlFor="nf-role" className="col-md-2">
                  Role
                </CLabel>
                <div className="col-md-9">
                  <Select
                    closeMenuOnSelect={true}
                    components={animatedComponents}
                    value={Roles}
                    onChange={(e) => dispatch(setFormUserAction("Roles", e.value))}
                    isMulti
                    options={dataComboRoles}
                  />
                </div>
              </CFormGroup>
            </CForm>
          </CModalBody>
          <CModalFooter>
            <CButton
              color="primary"
              onClick={stateUpdate ? prosesUserEdit : prosesuserAdd}
            >
              {stateUpdate ? "Update" : "Tambah"}
            </CButton>
            <CButton color="secondary" onClick={showModal}>
              Cancel
            </CButton>
          </CModalFooter>
        </CModal>
      </CRow>
    </div>
  );
};

export default User;
