import { http } from "src/common/helper";
import serviceName from "src/common/helper/serviceName";
import Swal from "sweetalert2";
import { loadingStatus } from "./commonAction";
export const getBobotKondisiAction = () => {
  return(dispatch) => {
    http
      .get(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcBobot}/GetBobotKondisis`
      )
      .then((res) => {
        let result = res.data;
        if (result.isSuccess) {
          let resultData = result.data;
          let tidak_layak = [];
          let cukup_layak = [];
          let layak = [];

          let bobotTdkLayakBawah = 0;
          let bobotTdkLayakAtas = 0;
          let bobotCukupLayakBawah = 0;
          let bobotCukupLayakAtas = 0;
          let bobotLayakBawah = 0;
          let bobotLayakAtas = 0;
          resultData.map((v, i) => {


            if (v.nama === "Tidak Layak Bawah") {
              bobotTdkLayakBawah = v.bobot;
            } else {
              bobotTdkLayakBawah = 10;
            }
            if (v.nama === "Tidak Layak Atas") {
              bobotTdkLayakAtas = v.bobot;
            } else {
              bobotTdkLayakAtas = 50;
            }

            if (v.nama === "Cukup Layak Bawah") {
              bobotCukupLayakBawah = v.bobot;
            } else {
              bobotCukupLayakBawah = 60;
            }

            if (v.nama === "Cukup Layak Atas") {
              bobotCukupLayakAtas = v.bobot;
            } else {
              bobotCukupLayakAtas = 80;
            }

            if (v.nama === "Layak Bawah") {
              bobotLayakBawah = v.bobot;
            } else {
              bobotLayakBawah = 90;
            }

            if (v.nama === "Layak Atas") {
              bobotLayakAtas = v.bobot;
            } else {
              bobotLayakAtas = 100;
            }


          });

            for (let i = bobotTdkLayakBawah; i <= bobotTdkLayakAtas; i++) {
              tidak_layak.push(i);
              i = i + 9;
            }

           for (let i = bobotLayakBawah; i <= bobotLayakAtas; i++) {
              layak.push(i);
              i = i + 9;
            }
              for (let i = bobotCukupLayakBawah; i <= bobotCukupLayakAtas; i++) {
              cukup_layak.push(i);
              i = i + 9;
            }
          dispatch({
            type: "BOBOT_KONDISI_TIDAK_LAYAK",
            payload: tidak_layak,
          });
          dispatch({
            type: "BOBOT_KONDISI_CUKUP_LAYAK",
            payload: cukup_layak,
          });
          dispatch({ type: "BOBOT_KONDISI_LAYAK", payload: layak });
        }
      });
  }
};


export const SetGetJawabanAction = (idMonev, idVariabel, idIndikator, dataJawaban) => {
  console.log(idIndikator);

  return (dispatch) => {
    let idUser = sessionStorage.getItem("idUser");
    let param = `?IdUser=${idUser}&IdMonev=${idMonev}&IdVariabel=${idVariabel}&IdIndikator=${idIndikator}`;
    console.log(param);
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMonev}/SetGetJawaban${param}`,
        dataJawaban
      )
      .then((res) => {
        loadingStatus(false);
        console.log(res.data);
        let data = res.data;
        if (data.isSuccess) {
          console.log(data.data);
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};


export const AddJawabanAction = (dataJawbans) => {
  console.log(dataJawbans[0]);
  return (dispatch) => {
    let idUser = sessionStorage.getItem("idUser");
    http
      .post(
        `${process.env.REACT_APP_ROOT_API}${serviceName.svcMonev}/AddJawaban?IdUser=${idUser}`,
        dataJawbans[0]
      )
      .then((res) => {
        loadingStatus(false);
        console.log(res.data);
        let data = res.data;
        if (data.isSuccess) {
          console.log(data.data);
        } else {
          Swal.fire("Gagal", `${data.returnMessage}`, "error");
        }
      });
  };
};





